'use client';

export default function UserPage() {
  return (
    <div className="p-4">
      <h1>用户中心</h1>
      <div className="flex items-center">
        <img src="/assets/images/avatar.jpg" alt="用户头像" className="w-16 h-16 rounded-full" />
        <div className="ml-4">
          <h2>用户名</h2>
          <p>余额: 0 珍珠币</p>
        </div>
      </div>
      <div className="mt-4">
        <button className="block w-full p-2 bg-blue-500 text-white rounded">注册</button>
        <button className="block w-full p-2 bg-green-500 text-white rounded mt-2">登录</button>
        <button className="block w-full p-2 bg-yellow-500 text-white rounded mt-2">充值</button>
      </div>
    </div>
  );
}

